import { createRouter, createWebHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/login',
    name: 'login',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/LoginView.vue')
  },
  {
    path: '/app/100067/buy/0',
    name: 'choose',
    component: () => import(/* webpackChunkName: "about" */ '../views/Choose.vue')
  }, {
    path: '/app/100067/buy/0/:telco',
    name: 'payment',
    component: () => import(/* webpackChunkName: "about" */ '../views/Payment.vue')
  },
        
  
  {
    path: '/app/100067/buy/0/:telco/item/:amount',
    name: 'recharge',
    component: () => import(/* webpackChunkName: "about" */ '../views/Recharge.vue')
  },
  {
    path: '/result/display_error/:telco/item/:amount',
    name: 'error',
    component: () => import(/* webpackChunkName: "about" */ '../views/Error.vue')
  },
  {
    path: '/result/display_success/:telco/item/:amount',
    name: 'success',
    component: () => import(/* webpackChunkName: "about" */ '../views/Success.vue')
  },
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
